@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
/* common.css */

/* Обнуляющие стили -------------------------------------------------------------------- */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: sans-serif;
	line-height: 1;
}

h1, h2, h3, h4, h5, h6 {
	font-size: inherit;
	font-weight: inherit;
}

ul, ol {
	list-style-type: none;
}

a {
	text-decoration: none;
	cursor: pointer;
}

button {
	font-size: inherit;

	border: none;
	background-color: transparent;
	cursor: pointer;
}

img {
	vertical-align: top;
}
/* ------------------------------------------------------------------------------------- */

/* Технические классы ------------------------------------------------------------------ */

._container {
	max-width: 1200px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
}

/* Для адаптивного вывода изображений через тег <img> */
._ibg {
	position: relative;
	/* padding-bottom: n%; */ /* Регулирует высоту изображения. */
}

._ibg img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

._hidden {
	display: none;
}

/* Как правило, текст для скринридеров, который не нужно выводить. */
._visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
}
/* ------------------------------------------------------------------------------------- */
/* app.css */

html {
	scroll-behavior: smooth;
}

/* header ------------------------------------------------------------------------------ */

.header__body {
	padding-top: 20px;
	padding-bottom: 20px;
}

.header__title {
	font-size: 24px;
	font-weight: bold;
	text-align: center;
}

/* main -------------------------------------------------------------------------------- */

.main {
	padding-bottom: 50px;
}

.main__item:not(:last-child) {
	margin-bottom: 50px;
}

.task {
	min-height: 100vh;
}

.task__header {
	display: flex;
	justify-content: center;
	align-items: center;

	padding-top: 20px;
	padding-bottom: 20px;
}

.task__title {
	margin-right: 30px;

	font-size: 20px;
	font-weight: bold;
	text-align: center;
}

.menu {
	display: flex;
	flex-direction: column;
}

.menu__item:not(:last-child) {
	margin-bottom: 10px;
}

.menu__link {
	position: relative;
	display: block;
	width: 34px;
	height: 15px;
}

.menu__link::before,
.menu__link::after {
	content: '';
	position: absolute;
	top: 5px;
	width: 20px;
	height: 4px;

	border-radius: 3px;
	background-color: lightgray;

	transition: background-color 0.3s;
}

.menu__link-bottom::before {
	left: 0;
	-webkit-transform: rotate(30deg);
	        transform: rotate(30deg);
}

.menu__link-bottom::after {
	right: 0;
	-webkit-transform: rotate(-30deg);
	        transform: rotate(-30deg);
}

.menu__link-top::before {
	left: 0;
	-webkit-transform: rotate(-30deg);
	        transform: rotate(-30deg);
}

.menu__link-top::after {
	right: 0;
	-webkit-transform: rotate(30deg);
	        transform: rotate(30deg);
}

.menu__link:hover::before,
.menu__link:hover::after {
	background-color: #a6a6a6;
}

.task__body {
	position: relative;
}

/* Общие стили */

/* form-watches.css */

.form-watches {
  max-width: 700px;
}

.form-watches__body {
  display: flex;
  margin: 0 -10px;
}

.form-watches__column {
  padding: 0 10px;
}

.form-watches__column-1,
.form-watches__column-2 {
  flex: 0 0 40%;
}

.form-watches__column-3 {
  flex: 0 0 20%;
  align-self: flex-end;
}

.item-form-watches__title {
  display: block;
  margin-bottom: 10px;

  text-align: center;
}

.item-form-watches__input {
  height: 30px;
  padding: 0 10px;

  font-size: 16px;
  text-align: center;
}

.item-form-watches__input,
.item-form-watches__btn {
  width: 100%;

  border: 1px solid gray;
  border-radius: 3px;
}

.item-form-watches__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 30px;

  font-size: 15px;
  line-height: 100%;
  font-weight: 700;

  transition: color .3s;
}

.item-form-watches__btn:hover {
  color: grey
}

@media(max-width: 560px) {
  .form-watches__body {
    display: block;
  }

  .form-watches__body > *:not(:last-child) {
    margin-bottom: 20px;
  }

  .item-form-watches__title {
    margin-bottom: 5px;
  }
}

/* item-watches.css */

.item-watches {
  padding: 15px;
}

.item-watches__title {
  text-align: center;
  margin-bottom: 5px;
}

.item-watches__main {
  position: relative;
}

.item-watches__time-digit {
  margin-bottom: 5px;

  font-size: 12px;
  text-align: center;
}

.item-watches__clock-face {
  margin: 0 auto;
}

.clock-face {
  width: 80px;
  height: 80px;

  border: 1px solid black;
  border-radius: 50%;
}

.item-watches__btn-remove {
  position: absolute;
  display: block;
  top: -3px;
  right: -15px;
  width: 20px;
  height: 20px;

  border: 1px solid grey;
  border-radius: 50%;

  transition-property: border-color;
  transition: .2s;
}

.item-watches__btn-remove:hover {
  border-color: black;
}

.item-watches__btn-remove::before,
.item-watches__btn-remove::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;

  width: 10px;
  height: 1px;

  background-color: gray;

  transition-property: background-color;
  transition: .2s;
}

.item-watches__btn-remove::before {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
} 

.item-watches__btn-remove::after {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
} 

.item-watches__btn-remove:hover::before,
.item-watches__btn-remove:hover::after {
  background-color: black;
}

.clock-face {
  position: relative;
}

.clock-face::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;

  border-radius: 50%;

  background-color: black;

  -webkit-transform: translate(-50%, -50%);

          transform: translate(-50%, -50%);
}

.clock-face__hand {
  position: absolute;
  top: 50%;
  left: 50%;

  background-color: black;

  -webkit-transform-origin: bottom;

          transform-origin: bottom;
}

.clock-face__hand::after {
  content: '';
  position: absolute;
  top: -2px;
  left: 0px;
  width: 0px;
  height: 0px;

  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-bottom: 6px solid black;

}

/* Угол поворота стрелки . */

.clock-face__hour-hand {
  width: 2px;
  height: 25px;

  -webkit-transform: translate(-50%, -100%) rotate(60deg);

          transform: translate(-50%, -100%) rotate(60deg); /* Угол поворота часовой стрелки. */
}

.clock-face__hour-hand::after {
  -webkit-transform: translate(-35%, 0);
          transform: translate(-35%, 0);
}

.clock-face__minute-hand {
  width: 1px;
  height: 35px;

  -webkit-transform: translate(-50%, -100%) rotate(0deg);

          transform: translate(-50%, -100%) rotate(0deg); /* Угол поворота минутной стрелки. */
}

.clock-face__minute-hand::after {
  -webkit-transform: translate(-40%, 0);
          transform: translate(-40%, 0);
}

.clock-face__second-hand {
  width: 1px;
  height: 35px;

  background-color: gray;

  -webkit-transform: translate(-50%, -100%) rotate(150deg);

          transform: translate(-50%, -100%) rotate(150deg); /* Угол поворота секундной стрелки. */
}

.clock-face__second-hand::after {
  display: none;
}

/* watches.css */

.watches {
  padding: 20px 0;
}

.watches__form {
  margin: 0 auto 20px auto;
}

.watches__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -15px 0 -15px -15px;
}


/* crud__header.css */

.crud__header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.crud__title {
  margin-right: 20px;

  font-size: 28px;
}

.crud__btn-udate {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 36px;
  height: 36px;

  font-size: 10px;

  border: 1px solid black;
  border-radius: 50%;

  color: rgb(63, 209, 63);
  opacity: 0.5;
  transition-property: opacity;
  transition: 0.2s;
}

.crud__btn-udate:hover {
  opacity: 1;
}

.crud__btn-udate_animated {
  -webkit-animation-name: update;
          animation-name: update;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

@-webkit-keyframes update {
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes update {
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.crud__link-udate .material-icons {
  font-size: 22px;
}

/* form-crud.css */

.form-new-note__header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.form-new-note__title {
  margin-right: 20px;

  font-size: 18px;
}

.form-new-note__btn-send {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 36px;
  height: 36px;

  border: 1px solid black;
  border-radius: 50%;  

  opacity: 0.5;

  transition-property: opacity;
  transition-duration: 0.2s;
}

.form-new-note__btn-send::after {
  content: '\27A4';
  font-size: 20px;
  line-height: 100%;

  -webkit-transform: translateX(1px);

          transform: translateX(1px);
}

.form-new-note__btn-send:hover {
  opacity: 1;
}

.form-new-note__input {
  max-width: 350px;
  width: 100%;
  padding: 10px;
}
/* note.css */

.note {
  position: relative;
  padding: 20px;

  border: 1px solid black;
}

.note__body {
  font-size: 14px;
  text-align: center;
}

.note__btn-remove {
  position: absolute;
  top: -15px;
  right: -15px;
  width: 30px;
  height: 30px;

  border: 1px solid #808080;
  border-radius: 50%;

  background-color: white;

  transition-property: border;
  transition-duration: 0.2s;
}

.note__btn-remove:hover {
  border: 1px solid #000000;
}

.note__btn-remove::before,
.note__btn-remove::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;

  width: 15px;
  height: 2px;

  background-color: #ff6666;

  transition-property: background-color;
  transition-duration: 0.2s;
}

.note__btn-remove::before {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
}

.note__btn-remove::after {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
}

.note__btn-remove:hover::before,
.note__btn-remove:hover::after {
  background-color: #ff0000;
}

@media(max-width: 479px) {
  .note {
    padding: 10px;
  }
}

/* crud.css */

.crud__notes {
  display: flex;
  flex-wrap: wrap;
  margin: -15px -10px 20px -15px;
}

.crud__note {
  flex: 0 0 20%;
  padding: 15px;
}

@media(max-width: 1100px) {
  .crud__note {
    flex: 0 0 25%;
    padding: 15px;
  }
}

@media(max-width: 900px) {
  .crud__note {
    flex: 0 0 33.33%;
    padding: 15px;
  }
}

@media(max-width: 639px) {
  .crud__note {
    flex: 0 0 50%;
    padding: 15px;
  }
}

@media(max-width: 479px) {
  .crud__notes {
    display: block;
    margin: -10px -5px -10px -10px;
  }

  .crud__note {
    flex: 0 0 50%;
    padding: 10px;
  }
}
